 export default {
 name: 'RaiseTempSearch',

 data () {
    return {
    searchMessage: "",
   }
  },

    methods: {

      showGrouping(category) {
        $nuxt.$emit("getCategory", category);
      },

      submitSearch: function(topic) {

        $nuxt.$emit("submitSearch", topic);
        this.searchMessage = '';

      }

    },

 }

